<!-- HTML Template -->
<template>
  <div id="app">
    <div class="container">
       <!--UPLOAD-->
       <div class="upload-box">
        <form enctype="multipart/form-data" novalidate v-if="isInitial || isSaving">
          <h1>Upload Files</h1>
          <div class="dropbox">
            <input type="file" multiple :name="uploadFieldName" :disabled="isSaving" @change="filesChange($event.target.name, $event.target.files); fileCount = $event.target.files.length"
              accept=".jpg,.txt" class="input-file">
              <p v-if="isInitial">
                Drag your file(s) here to begin<br> or click to browse
              </p>
              <p v-if="isSaving">
                Uploading {{ fileCount }} files...
              </p>
          </div>
        </form>
      </div>
      
      <div class="file-box">
        <input type="text" id="txtFileName" v-model="fileName" placeholder="Nhập tên file cần download"> 
      </div>
      <div class="download-box">
          <h1>Download Files</h1>
          <div>
            <input id="btnDownload" type="button" @click="downloadFile" value="Download File"/>
          </div>
      </div>

      <div class="delete-box">
          <h1>Delete Files</h1> 
          <div>
            <input id="btnDelete" type="button" @click="deleteFile" value="Delete File"/>
          </div>
      </div>
      
      <!--SUCCESS-->
      <div v-if="isSuccess">
        <h2>Uploaded {{ uploadedFiles.length }} file(s) successfully.</h2>
        <p>
          <a href="javascript:void(0)" @click="reset()">Upload again</a>
        </p>
        <ul class="list-unstyled">
          <li v-for="item in uploadedFiles" :key="item.id">
            <img :src="item.url" class="img-responsive img-thumbnail" :alt="item.originalName">
          </li>
        </ul>
      </div>
      <!--FAILED-->
      <div v-if="isFailed">
        <h2>Uploaded failed.</h2>
        <p>
          <a href="javascript:void(0)" @click="reset()">Try again</a>
        </p>
        <pre>{{ uploadError }}</pre>
      </div>
    </div>
  </div>
</template>
<!-- Javascript -->
<script> 
  import * as axios from 'axios'; 

  const STATUS_INITIAL = 0, STATUS_SAVING = 1, STATUS_SUCCESS = 2, STATUS_FAILED = 3;

  export default {
    name: 'app',
    data() {
      return {
        uploadedFiles: [],
        uploadError: null,
        currentStatus: null,
        uploadFieldName: 'photos',
        fileName: ""
      }
    },
    computed: {
      isInitial() {
        return this.currentStatus === STATUS_INITIAL;
      },
      isSaving() {
        return this.currentStatus === STATUS_SAVING;
      },
      isSuccess() {
        return this.currentStatus === STATUS_SUCCESS;
      },
      isFailed() {
        return this.currentStatus === STATUS_FAILED;
      }
    },
    methods: {
      reset() {
        // reset form to initial state
        this.currentStatus = STATUS_INITIAL;
        this.uploadedFiles = [];
        this.uploadError = null;
      },
      save(formData) {
        // upload data to the server
        this.currentStatus = STATUS_SAVING;
        this.upload(formData)
          .then(x => {
            this.uploadedFiles = [].concat(x);
            this.currentStatus = STATUS_SUCCESS;
          })
          .catch(err => {
            this.uploadError = err.response;
            this.currentStatus = STATUS_FAILED;
          });
      },
       /* eslint-disable */
      filesChange(fieldName, fileList) {
        debugger;
        // handle file changes
        const formData = new FormData();

        if (!fileList.length) return;

        // append the files to FormData
        Array
          .from(Array(fileList.length).keys())
          .map(x => {
            formData.append(fieldName, fileList[x], fileList[x].name);
          });
        // save it
        this.save(formData);
      },
      downloadFile(){ 
         this.download(this.fileName);
      },
      upload(formData) {
        const url = `${ window.__config.APIFileURL}/upload`;
        return axios.post(url, formData)
            // get data
            .then((response) => {
              if(response.data && response.data.success){
                this.currentStatus = STATUS_SUCCESS;
                 alert("Upload thành công");
              } else {
                 alert("Upload thất bại");
              }
            }) 
            .catch(ex => {
              console.log(ex)
            });
      }, 
      download(filename) {
        const url = `${ window.__config.APIFileURL}/download?filename=${filename}`;
        // { responseType: "blob" }
         axios({
                    url: url,
                    method: 'GET',
                    responseType: 'blob',
                }).then((response) => {
                     var fileURL = window.URL.createObjectURL(new Blob([response.data]));
                     var fURL = document.createElement('a');
   
                     fURL.href = fileURL;
                     fURL.setAttribute('download', filename);
                     document.body.appendChild(fURL);
   
                     fURL.click();
                    URL.revokeObjectURL(fURL.href);
                }).catch(ex => {
                  console.log(ex)
                  });
      },
      deleteFile(){
        const url = `${ window.__config.APIFileURL}/delete/${this.fileName}`;
          axios({
                    url: url,
                    method: 'GET'
                }).then((response) => {
                   if(response){
                      alert("Xóa thành công");
                   }else { 
                      alert("Xóa thất bại");
                   }
                }).catch(ex => {
                  console.log(ex)
                  });
      } 
    },
    mounted() {
      this.reset();
    },
  }

</script>

<style>
  .dropbox {
    outline: 2px dashed grey; /* the dash box */
    outline-offset: -10px;
    background: lightcyan;
    color: dimgray;
    padding: 10px 10px;
    min-height: 200px; /* minimum height */
    position: relative;
    cursor: pointer;
  }
  
  .input-file {
    opacity: 0; /* invisible but it's there! */
    width: 100%;
    height: 200px;
    position: absolute;
    cursor: pointer;
  }
  
  .dropbox:hover {
    background: lightblue; /* when mouse over to the drop zone, change color */
  }
  
  .dropbox p {
    font-size: 1.2em;
    text-align: center;
    padding: 50px 0;
  }
</style>
